import { h, Component } from 'preact';
import { useEffect } from 'preact/hooks';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

import Header from './header';
import Submenu from './submenu';
import Heatmap from '../../../components/public/heatmap';
import RaceHeader from './raceheader';
import RaceText from './raceText';
import DisplayMusher from './displayMusher';
import ListMusher from './listMusher';
import Gauge from '../../../components/gauge';

import mu from '../../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true, true);
const MARGIN_BOTTOM = mu.marginBottom();

const MARKDOWN_OPTIONS = {
	pedantic: false,
	gfm: true,
	breaks: true,
	sanitize: false,
	smartLists: true,
	smartypants: true,
	xhtml: true,
};

function PageTitle(title, classes = '', style = '') {
	useEffect(() => {
		document.title = `Homerun Race: ${title}`;
	}, [title]);
	return <span class={classes} style={style}>{title}</span>;
}

function getWindowDimensions() {
    const { innerWidth: width, innerHeight: height } = window;
    return {
        width,
        height
    };
}

const initialState = {
	isLoading: false,
	email: '',
	submenu: 'ads',
	jwtToken: util.getJwtToken(),

	reloadInSec: null,
	reloadIsActive: false,

	leaderboardMode: 'mushers',
	filterByraceClass: '',
	showLeaderBoard: true,
	showNews: true,
};

const RELOAD_INTERVAL_IN_SEC = 60;

@observer
class ReloadTimer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.updateTimer = {};
    }

	initComponent = () => {
		this.setState({
			reloadIsActive: true,
		}, () => this.tickReloadTimeer());
	}

	tickReloadTimeer = () => {
        const { reloadLastEpoch } = this.props;
		const { reloadIsActive } = this.state;
        if (reloadIsActive) {
			const now = Math.floor(new Date().getTime() / 1000);
			const reloadInSec = RELOAD_INTERVAL_IN_SEC - (now - reloadLastEpoch);
			this.setState({ reloadInSec });
			clearTimeout(this.updateTimer.tick);
			this.updateTimer.tick = setTimeout(() => this.tickReloadTimeer(), 1000);
        }
		return false;
    }

	componentDidMount() {
		this.initComponent();
	}

	componentWillUnmount() {
		clearTimeout(this.updateTimer.tick);
	}

	render() {
		const { reloadInSec } = this.state;
		const { radius = 13, fontSize = 11, classNames = '' } = this.props;
		const reloadPercent = Math.floor(util.normalizeBetween(reloadInSec, 0, RELOAD_INTERVAL_IN_SEC, 0, 100))

		return (<>		
			<span class={classNames}>
				<Gauge key={`gauge-reload-page`}
					color='primary'
					radius={radius}
					showPercentText={false}
					percent={reloadPercent}
					showCenterText={true}
					centerText={reloadInSec}
					centerTextSize={fontSize}
					centerTextY={'55%'}
				/>
			</span>
		</>);
	}
}

@observer
class PublicRaceDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
			reloadLastEpoch: Math.floor(new Date().getTime() / 1000),
        };
		this.updateTimer = {};
    }

	loadLiveRaceResults = async (props = this.props, focusOnMushers) => {
		const { raceStore } = props.stores;
		const { race } = raceStore;
		await raceStore.getRaceTrackerData(race.raceTrackerRace);
		raceStore.getRaceTrackerActiveTeams();
	}

	loadRaceResults = async (props = this.props, teamIds, focusOnMushers) => {
		const { raceStore } = props.stores;
		const { raceid } = props;

		await raceStore.loadWorkoutSummary(teamIds, raceid);
		raceStore.getWorkoutSummaryActiveTeams(focusOnMushers);
	}

	async loadRace(props = this.props) {
		this.setState({ isLoading: true });
		const { raceStore, appState } = props.stores;
		const { raceid } = props;
		await raceStore.load(raceid);
		const { race } = raceStore;
		const { focusOnMushers = [] } = appState;

		const allTeamIds = race.classes.filter(currentClass => currentClass.contestants && currentClass.contestants.length > 0).map((currentClass) => {
            return currentClass.contestants.filter(musher => musher.team > 0).map(musher => musher.team);
		}).flat();

		const teamsInThisRace = focusOnMushers.filter(teamid => allTeamIds.indexOf(teamid) > -1);
		this.setState({ allTeamIds });
		if (allTeamIds.length > 0) {
			await raceStore.loadTeams(allTeamIds.join(','));
			if (race.liveRace > 0) {
				await this.loadLiveRaceResults(props, focusOnMushers);

			} else {
				await this.loadRaceResults(props, allTeamIds.join(','), focusOnMushers);
			}

		}
		raceStore.getAllClassesSortedBy();

		// Set reload timer
		clearTimeout(this.updateTimer.reload);
		this.updateTimer.reload = setTimeout(async () => {
			const reloadLastEpoch = Math.floor(new Date().getTime() / 1000);
			this.setState({ reloadLastEpoch });
			this.loadRace();
		}, RELOAD_INTERVAL_IN_SEC * 1000);

		this.setState({ isLoading: false });
	}

	setLeaderboardMode = (e) => {
		const leaderboardMode = e.target.parentElement.dataset.mode || e.target.dataset.mode;
		this.setState({ leaderboardMode })
	}

	chooseRaceClass = (e) => {
		const filterByraceClass = e.target.value;
		this.setState({ filterByraceClass })
	}

	toggleShowLeaderBoard = (hide) => {
		const { showLeaderBoard } = this.state;
		if (hide === true) {
			this.setState({ showLeaderBoard: false });
		} else {
			this.setState({ showLeaderBoard: !showLeaderBoard });
		}
	}

	toggleShowNews = (hide) => {
		const { showNews } = this.state;
		if (hide === true) {
			this.setState({ showNews: false });
		} else {
			this.setState({ showNews: !showNews });
		}
	}

	componentWillUnmount() {
		clearTimeout(this.updateTimer.reload);
		clearTimeout(this.updateTimer.tick);
	}

	componentDidMount() {
		this.loadRace();
		const { width, height } = getWindowDimensions();
		if (width < 800) {
			this.toggleShowLeaderBoard(true);
			this.toggleShowNews(true);
		}
	}

	componentWillReceiveProps(nextProps) {
		this.loadRace(nextProps);
	}

	render() {
		const { appState, raceStore, userStore } = this.props.stores;
		const { user = {} } = userStore;
		const { raceid, path } = this.props;
		const { language = 'en', focusOnMushers = [], infoOnMushers = [] } = appState;
		const { leaderboardMode, filterByraceClass, showLeaderBoard, showNews, reloadLastEpoch, allTeamIds = [] } = this.state;

		const teamsInThisRace = focusOnMushers.filter(teamid => allTeamIds.indexOf(teamid) > -1);

		const { activeTeams, teamList, waypoints, zoomToMarkers, race, classes } = raceStore;
		return (
			<>
				<div class='container-fluid h-100 position-relative p-0'>
					<Heatmap
						stores={this.props.stores}
						raceid={raceid}
						waypoints={waypoints}
						zoomToMarkers={zoomToMarkers}
						fullscreenMap={true}
						// key={`heatmap-${focusOnMushers.join('-')}-${reloadLastEpoch}`}
						// mapid={`heatmap-${focusOnMushers.join('-')}-${reloadLastEpoch}`}
					/>

					<div class='position-absolute' style='top: 15px; left: 5px; z-index: 99999999;'>
						<div class='logo ml-3 clearfix text-white'>
							<img src={`./assets/homerun-race-logo-2-small.png`} height='37' class='float-left' />
							<a href={`/race/${race.id}`}>
								<span class='font-weight-bold text-dark' style='font-size: 1.4em;'>Homerun Race</span>
							</a>
						</div>
					</div>

					{!showLeaderBoard && <div class='position-absolute' style='top: 55px; left: 5px; z-index: 99999999; width: 150px; height: 50px;'>
						<button type='button' class='btn btn-sm btn-light' onClick={this.toggleShowLeaderBoard}>
		                    <ReloadTimer stores={this.props.stores} classNames='float-right ml-2' reloadLastEpoch={reloadLastEpoch} />
							<span class='float-left'>Leaderboard</span>
						</button>
					</div>}

					{!showNews && <div class='position-absolute' style='bottom: 40px; right: 5px; z-index: 99999999; width: 150px; height: 50px;'>
						<button type='button' class='btn btn-sm btn-primary float-right' onClick={this.toggleShowNews}>
							<span class='float-left'>News</span>
						</button>
					</div>}

					{showNews && race && race.articles && race.articles && race.articles.length >  0 && <div class='position-absolute' style='bottom: 50px; right: 5px; z-index: 99999999; width: 350px; height: 40vh;'>
						<div class='container-fluid bg-light rounded-lg py-2 overflow-auto mt-2' style='opacity: 0.9; height: 90%;'>
							<div class='row bg-light'>
								<div class='col-12 bg-primary text-white'>
									<button type='button' class='btn btn-sm btn-danger float-right' onClick={this.toggleShowNews}>
										X
									</button>
									News
								</div>
								<div class='col-12 bg-light'>
									{race && race.articles && race.articles.reverse().map(article => <div class='border-bottom mb-2 pb-2 clearfix'>
										<small class='text-muted float-right'>{util.formatDate(article.date, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}</small>
										<h5>{mu.replaceImages(article.title, article.images, language, false)}</h5>
										{article.image && <img src={article.image.s3LargeLink} class='float-right rounded-lg ml-2' style='height: 100px;' />}

										{article.ingress && <strong>
											<Markdown markdown={mu.replaceImages(article.ingress, article.images, language)} markdownOpts={MARKDOWN_OPTIONS} />
										</strong>}
										{article.body && <p>
											<Markdown markdown={mu.replaceImages(article.body, article.images, language)} markdownOpts={MARKDOWN_OPTIONS} />
										</p>}
									</div>)}
								</div>
							</div>
						</div>
					</div>}

					{showLeaderBoard && <div class='position-absolute' style='top: 55px; left: 5px; z-index: 99999999; width: 350px; height: 90vh;'>
						<div class='container-fluid bg-light rounded-lg py-2 overflow-auto mt-2' style='opacity: 0.9; height: 90%;'>
							<div class='row bg-light'>
								<div class='col-12'>
									<button type='button' class='btn btn-sm btn-link float-right' onClick={this.toggleShowLeaderBoard}>
										X
									</button>

									<ReloadTimer stores={this.props.stores} classNames='float-right' radius={15} fontSize={13} reloadLastEpoch={reloadLastEpoch} />
									<h5 class='mb-0'>Leaderboard</h5>
									{PageTitle(race.title, 'font-weight-normal text-dark', 'font-size: 1.0em;')}

									<select class='form-control-plaintext' aria-describedby='classHelp'
										onInput={this.chooseRaceClass}
									>
										<option value=''>-- Choose class --</option>
										{race.classes && race.classes.sort((a, b) => a.distance - b.distance).map(currentClass => <>
											<option value={currentClass.id}>{currentClass.title} ({currentClass.totalMushers})</option>
										</>)}
									</select>

									<ul class='mt-2 nav nav-tabs nav-justified tracking-nav'>
										<li class='nav-item' style='width: 33%;'>
											<a class={`nav-link ${leaderboardMode === 'mushers' ? 'active' : ''}`} onClick={this.setLeaderboardMode} data-mode='mushers'><Text id='races.mushers'>Deltakere</Text></a>
										</li>
										<li class='nav-item' style='width: 33%;'>
											<a class={`nav-link ${leaderboardMode === 'onTrail' ? 'active' : ''}`} onClick={this.setLeaderboardMode} data-mode='onTrail'><Text id='races.on-trail'>På sporet nå</Text></a>
										</li>
										<li class='nav-item' style='width: 33%;'>
											<a class={`nav-link ${leaderboardMode === 'favorites' ? 'active' : ''}`} onClick={this.setLeaderboardMode} data-mode='favorites'><Text id='races.favorites'>Favoritter</Text></a>
										</li>
									</ul>
								</div>
							</div>

							{leaderboardMode === 'mushers' && <>

								{race.classes && race.classes.sort((a, b) => a.distance - b.distance)
									.filter((currentClass) => {
										if (filterByraceClass) {
											if (filterByraceClass === currentClass.id) {
												return true;
											}
											return false;
										}
										return true;
									})
									.map(currentClass => <>
										<div class='row mt-2 bg-primary text-white'>
											<div class='col-12'>
												<h5 class='m-0'>
													{currentClass.title}
												</h5>
											</div>
										</div>
										{classes[currentClass.id] && classes[currentClass.id].contestants && classes[currentClass.id].contestants.map((contestant, idx) => {
											const { musher, currentTeam, currentStages, currentResults, completed, duration,
												normalizedDuration, weightedNormalizedDuration, totalKcalRelated, weightedTotalKcal,
												flatTotalKcal, percentHarder, loadIndex, raceDistance, isDone } = contestant;
											return (<>
												<ListMusher stores={this.props.stores} team={currentTeam} idx={idx} />
											</>);
										})}
									</>)}


								{/* {(teamList && teamList.length > 0) ? teamList.filter((team) => {
									if (filterByraceClass) {
										const raceClass = raceStore.getClassByTeamId(team.id);
										if (filterByraceClass === raceClass.id) {
											return true;
										}
										return false;
									}
									return true;
								}).map((team, idx) => <ListMusher stores={this.props.stores} team={team} idx={idx} />) : <>
									<div class='p-2 text-center'>
										<h5><Text id='races.no-teams-signed-up'>Ingen team påmeldt ennå...</Text></h5>
									</div>
								</>} */}
							</>}

							{leaderboardMode === 'favorites' && <>
								{(teamsInThisRace && teamsInThisRace.length > 0) ? teamsInThisRace.map((teamId, idx) => {
										const team = raceStore.getTeamById(teamId);
										return <ListMusher stores={this.props.stores} team={team} idx={idx} />;
									}	
								) : <>
									<div class='p-2 text-center'>
										<h5><Text id='races.no-teams-signed-up'>Ingen team påmeldt ennå...</Text></h5>
									</div>
								</>}
							</>}

							{leaderboardMode === 'onTrail' && <>
								<div class='text-muted text-center bg-light'><Text id='races.mushers-on-trail'>Mushers on trail</Text>: {activeTeams.length}</div>
								{(activeTeams && activeTeams.length > 0) ? activeTeams.filter((line) => {
									if (filterByraceClass) {
										const raceClass = raceStore.getClassByTeamId(line.team);
										if (filterByraceClass === raceClass.id) {
											return true;
										}
										return false;
									}
									return true;
								}).map((line, idx) => {
									const team = raceStore.getTeamById(line.team);
									return (<ListMusher stores={this.props.stores} team={team} idx={idx} />);
								}) : <>
									<div class='p-2 text-center'>
										<h5><Text id='races.no-team-on-trail'>Ingen team tracker live i The Musher appen akkurat nå...</Text></h5>
									</div>
								</>}
							</>}
						</div>
					</div>}

					{(infoOnMushers && infoOnMushers.length > 0 && teamList && teamList.length > 0) && <>
						<div class='position-absolute overflow-auto' style='top: 15px; right: 55px; z-index: 99999999; max-width: 830px; max-height: 90%;'>
							<div class=''>
								<div class='d-flex flex-wrap'>
									{infoOnMushers.map(teamId => <DisplayMusher stores={this.props.stores} teamId={teamId} />)}
								</div>
							</div>
						</div>
					</>}

				</div>

			</>
		);
	}
}

export default PublicRaceDetail;
