import { h, Component } from 'preact';
import { useEffect } from "preact/hooks";
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

const initialState = {
};

@observer
class DisplayMusher extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	toggleFocusOnMusher = (e) => {
		const musherId = e.target.parentElement.dataset.musherid || e.target.dataset.musherid;
		const { appState, raceStore } = this.props.stores;
		appState.toggleFocusOnMusher(parseInt(musherId, 10));

		const { focusOnMushers } = appState;
		raceStore.getWorkoutSummaryActiveTeams(focusOnMushers);
	}

	toggleInfoOnMusher = (e) => {
		const musherId = e.target.parentElement.dataset.musherid || e.target.dataset.musherid;
		const { appState } = this.props.stores;
		appState.toggleInfoOnMusher(parseInt(musherId, 10));
	}

    render() {
		const { raceStore, appState } = this.props.stores;
		const { teamId } = this.props;
		const { darkmode, language = 'en' } = appState;
		const { race } = raceStore;

        const team = raceStore.getTeamById(teamId);
        if (!team || !team.id) {
            return <></>;
        }
        const line = raceStore.getActiveTeamById(team.id);
        const currentStages = raceStore.getWorkoutSummaryByTeam(team.id).reverse();
        const currentClass = raceStore.getClassByTeamId(team.id);
        const currentResults = raceStore.getWorkoutResultsByTeam(currentStages, currentClass.distance);
        const musher = raceStore.getMusherByTeamId(team.id);
        const image = team.images && team.images.length > 0 ? team.images[0] : {};
        const raceClass = raceStore.getClassByTeamId(team.id);

        const completed = currentResults.distanceKm / currentClass.distance * 100;
        const hasStarted = completed > 0 ? 1 : 0;
        const isDone = completed >= 100 ? 1 : 0;

        let currentSpeed;
        let updated;
        let isActive;
        if (line && line.currentLocation && line.currentLocation.coords) {
            currentSpeed = line.currentLocation.coords.speed * 3.6;
            updated = line.currentLocation.timestamp;
            isActive = true;
        }
        let toFinish = raceClass.distance - currentResults.distanceKm;
        if (toFinish < 0) {
            toFinish = 0;
        }

        // const currentSpeed = util.format(line.currentLocation.coords.speed * 3.6, 1);
        // avgDogs: 9.777777777777779
        // distanceKm: 268.3432364824406
        // duration: 67524.34002106966
        // elevation: 5287.123805500813
        // flatTotalKcal: 4971.658025959346
        // images: Array(32) [ Proxy, Proxy, Proxy, … ]
        // loadIndex: 1446
        // rest: 2306
        // speedAvg: 14.820718823260847
        // totalKcalRelated: 1225.314525471911
        // weightedTotalKcal: 11344.398317216459


		return (
			<>
                <div class='mr-2 mb-2 overflow-auto' style='width: 350px !important; max-height: 350px; overflow-x: hidden !important;'>
                    <div class='p-2 bg-light rounded-lg'>
                        <div class='row mt-2' style='font-size: 0.9em;'>															
                            <div class='col-4 text-center'>
                                <div
                                    class={`text-center text-muted pt-2 h-100`}
                                    style={`background-image: url("${image.s3SmallLink}"); background-size: cover;`}
                                    onClick={this.toggleFocusOnMusher} data-musherid={team.id}
                                >
                                    {!image.s3SmallLink && <>
                                        <span class='font-weight-lighter' style='font-size: 55px;'>
                                            {util.ucfirst(String(musher.firstname), true)}
                                            {util.ucfirst(String(musher.lastname), true)}
                                        </span>
                                    </>}
                                </div>
                            </div>
                            <div class='col-8'>
                                <div class='row'>																
                                    <div class='col-12' style='font-size: 1.5em; line-height: 1.0em;'>
                                        <button
                                            type='button'
                                            class='btn btn-sm float-right'
                                            onClick={this.toggleInfoOnMusher} data-musherid={team.id}
                                        >
                                            X
                                        </button>
                                        <span class='font-weight-bold'>{musher.firstname} {musher.lastname}</span><br />
                                        <span class='font-weight-lighter'>{team.name}</span><br />
                                        {raceClass && raceClass.title && <>
                                            <span class='font-weight-lighter mr-2' style='font-size: 0.8em;'>
                                                {raceClass.title} - 
                                            </span>
                                        </>}
                                        <span class='font-weight-lighter' style='font-size: 0.8em;'>
                                            {util.format(currentResults.avgDogs, 1)} <i class='fas fa-dog text-muted ml-1' />
                                        </span>
                                    </div>
                                </div>
                                <div class='row mt-2'>
                                    <div class='col-4 text-center' style='line-height: 1.4em;'>
                                        <span class='text-muted'>km/t</span><br />
                                        <span style='font-size: 2.0em;'>{util.format(currentResults.speedAvg, 1)}</span>
                                    </div>
                                    <div class='col-4 text-center' style='line-height: 1.4em;'>
                                        <span class='text-muted'>km</span><br />
                                        <span style='font-size: 2.0em;'>{util.format(currentResults.distanceKm)}</span>
                                    </div>
                                    <div class='col-4 text-center' style='line-height: 1.4em;'>
                                        <span class='text-muted'>to finish</span><br />
                                        <span style='font-size: 2.0em;'>
                                            {toFinish === 0 ? <i class='fas fa-flag-checkered text-success' /> : <>{util.format(toFinish, 0)}</>}
                                        </span>
                                    </div>
                                </div>
                                <div class='row mt-2'>
                                    <div class='col-6 text-center' style='line-height: 1.4em;'>
                                        <span class='text-muted'>
                                            {toFinish === 0 ? <>
                                                Calc.Duration
                                            </> : <>
                                                Duration
                                            </>}
                                        </span><br />
                                        <span style='font-size: 1.5em;'>
                                            {toFinish === 0 ? <>
                                                {util.secToHms(currentResults.weightedNormalizedDuration || currentResults.duration)}
                                            </> : <>
                                                {util.secToHms(currentResults.duration)}
                                            </>}
                                        </span>
                                    </div>
                                    <div class='col-6 text-center' style='line-height: 1.4em;'>
                                        <span class='text-muted'>Status</span><br />
                                        <span style='font-size: 1.5em;'>
                                            {isActive ? <>
                                                {line.isInProgress === 1 ? <span class='text-primary'>running</span> : ''}
                                                {line.isInProgress === 2 ? <span class='text-danger'>resting</span> : ''}
                                                {toFinish === 0 ? <>
                                                    {!line.isInProgress ? <span class='text-success'>FINISH</span> : ''}
                                                </> : <>
                                                    {!line.isInProgress ? <>
                                                        {hasStarted ? <span class='text-danger'>resting</span> : <span class='text-muted'>NYS</span>}
                                                    </> : '-'}                                                    
                                                </>}
                                            </> : <>
                                                {toFinish === 0 ? <span class='text-success'>FINISH</span> : <>
                                                    {hasStarted ? <span class='text-danger'>resting</span> : <span class='text-muted'>NYS</span>}
                                                </>}
                                            </>}
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class='row mt-2' style='font-size: 0.9em;'>															
                            <div class='col-6'>
                                <table class='table table-sm table-striped'>
                                    <tbody>
                                        <tr>
                                            <td>Current speed</td>
                                            <td>{currentSpeed ? <>{util.format(currentSpeed, 1)} km/t</> : '-'}</td>
                                        </tr>
                                        <tr>
                                            <td>Avg speed</td>
                                            <td>{util.format(currentResults.speedAvg, 1)} km/t</td>
                                        </tr>
                                        <tr>
                                            <td>Duration</td>
                                            <td>{util.secToHms(currentResults.duration)}</td>
                                        </tr>
                                        <tr>
                                            <td>Calculated duration</td>
                                            <td>{util.secToHms(currentResults.weightedNormalizedDuration)}</td>
                                        </tr>
                                        <tr>
                                            <td>Rest</td>
                                            <td>{util.secToHms(currentResults.rest)}</td>
                                        </tr>
                                        <tr>
                                            <td>Elevation</td>
                                            <td>{util.format(currentResults.elevation, 0)} m</td>
                                        </tr>
                                        <tr>
                                            <td>Altitude</td>
                                            <td>{isActive ? <>{line.currentLocation.coords.altitude} moh</> : '-'}</td>
                                        </tr>
                                        <tr>
                                            <td>Load index</td>
                                            <td>{currentResults.loadIndex}</td>
                                        </tr>
                                        <tr>
                                            <td>Updated:</td>
                                            <td>{isActive ? <>{util.formatDistance(updated, new Date())} ago</> : '-'}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <div class='col-6'>
                                <span>Elevation profile:</span><br />
                                {isActive ? <svg viewBox='0 0 300 100' style='width: 180px; height: 60px; top: 0; left: 0;'>
                                    <path d={`${line.svgElevation}`} style='fill: #0063FF; stroke: #0063FF; stroke-width: 2; opacity: 0.2;' />
                                </svg> : <div>-</div>}
                                <span>Map profile:</span><br />
                                {isActive ? <svg viewBox='0 0 300 300' style='width: 180px; height: 180px; top: 0; left: 0;'>
                                    <path d={`${line.svgMap}`} style='fill: none; stroke: #FF6300; stroke-width: 5; opacity: 0.2;' />
                                </svg> : <div>-</div>}
                            </div>
                        </div>

                        <div class='mt-2' style='font-size: 0.9em;'>															

                            <div class='d-flex flex-wrap'>
                                {currentStages && currentStages.map((stage, idx) => {
                                    const percent = stage.distanceKm / currentClass.distance * 100;
                                    const width = percent > 100 ? 100 : parseInt(percent, 10);
                                    const currentEpoch = Math.floor(new Date(stage.date).getTime() / 1000);
                                    const inDate = new Date(0); // The 0 there is the key, which sets the date to the epoch
                                    inDate.setUTCSeconds(currentEpoch + parseInt(stage.duration, 10));
                                    const images = stage.images.flat();
                                    if (1) {
                                        return (<>
                                            <div class='bg-light border rounded-lg text-right position-relative mr-1 mb-1'>
                                                <svg viewBox='0 0 300 100' class='position-absolute' style='width: 100%; height: 95%; bottom: 0px; left: 0px; z-index: 0;'>
                                                    <path d={`${stage.svgElevation}`} style='fill: #0063FF; stroke: #0063FF; stroke-width: 2; opacity: 0.2;' />
                                                </svg>
                                                <div class='position-relative px-3'>
                                                    <span class='position-absolute' style='top: 3px; left: 3px; font-size: 1.3em;'>{idx + 1}</span>
                                                    <small>
                                                        <span class='float-left'>
                                                            <nobr><i style='width: 20px;' class='fas fa-sign-out-alt' /> {util.formatDate(stage.date, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}</nobr>
                                                            <nobr><i style='width: 20px;' class='fas fa-sign-in-alt' /> {util.formatDate(inDate, { locale: language, hour12: false, hour: '2-digit', minute: '2-digit' })}</nobr>
                                                        </span>
                                                        <span class='float-right'>
                                                            {/* {restTimeText}
                                                            {restTimeTrackText} */}
                                                            <nobr><i style='width: 20px;' class='fas fa-road' /> {util.format(stage.distanceKm, 1) || 'n/a'} <span class='font-weight-lighter'>km</span></nobr>
                                                            <nobr><i style='width: 20px;' class='fas fa-mountain' /> {util.format(stage.elevation, 0) || 'n/a'} <span class='font-weight-lighter'>m</span></nobr>
                                                            <nobr><i style='width: 20px;' class='fas fa-tachometer-alt' /> {util.format(stage.speedAvg, 1) || 'n/a'} <span class='font-weight-lighter'>km/t</span></nobr><br />

                                                            <nobr><i style='width: 20px;' class='fas fa-clock' /> {util.secToHms(stage.duration) || 'n/a'}</nobr>
                                                            <nobr><i style='width: 20px;' class='fas fa-moon' /> {util.secToHms(stage.rest) || 'n/a'}</nobr><br />
                                                            <nobr><i style='width: 20px;' class='fas fa-dog' /> {util.format(stage.avgDogs, 0) || 'n/a'}</nobr>
                                                            <nobr><i style='width: 20px;' class='fas fa-balance-scale' /> {util.format(stage.loadIndex, 0) || 'n/a'}</nobr><br />
                                                            <nobr><i style='width: 20px;' class='fas fa-images' /> {images.length}</nobr><br />
                                                        </span>
                                                    </small>
                                                </div>
                                            </div>
                                        </>);
                                    }
                                    return (<>
                                        <div class='bg-light border rounded-lg text-right position-relative px-3 mr-1 mb-1' style={`width: ${width}%; min-width: 320px;`}>
                                            <small>
                                                <span class='float-left'>{idx + 1}</span>
                                                <nobr><i style='width: 20px;' class='fas fa-road' /> {util.format(stage.distanceKm, 1) || 'n/a'} <span class='font-weight-lighter'>km</span></nobr>
                                                <nobr><i style='width: 20px;' class='fas fa-tachometer-alt' /> {util.format(stage.speedAvg, 1) || 'n/a'} <span class='font-weight-lighter'>km/t</span></nobr>
                                                <nobr><i style='width: 20px;' class='fas fa-clock' /> {util.secToHms(stage.duration) || 'n/a'}</nobr>
                                            </small>
                                        </div>																		
                                    </>);
                                })}
                            </div>

                        </div>

                    </div>
                </div>
			</>
		);
	}
}

export default DisplayMusher;
