import { h, Component } from 'preact';
import { useEffect } from "preact/hooks";
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

const initialState = {
};

@observer
class ListMusher extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	toggleFocusOnMusher = (e) => {
		const musherId = e.target.parentElement.dataset.musherid || e.target.dataset.musherid;
		const { appState, raceStore } = this.props.stores;
		appState.toggleFocusOnMusher(parseInt(musherId, 10));

		const { focusOnMushers } = appState;
		raceStore.getWorkoutSummaryActiveTeams(focusOnMushers);
	}

	toggleInfoOnMusher = (e) => {
		const musherId = e.target.parentElement.dataset.musherid || e.target.dataset.musherid;
		const { appState } = this.props.stores;
		appState.toggleInfoOnMusher(parseInt(musherId, 10));
	}

    render() {
		const { raceStore, appState } = this.props.stores;
		const { team = {}, idx } = this.props;
		const { darkmode, language = 'en', focusOnMushers = [], infoOnMushers = [] } = appState;
		const { race } = raceStore;

        const line = raceStore.getActiveTeamById(team.id);
        const currentStages = raceStore.getWorkoutSummaryByTeam(team.id);
        const currentClass = raceStore.getClassByTeamId(team.id);
        const currentResults = raceStore.getWorkoutResultsByTeam(currentStages, currentClass.distance);
        const musher = raceStore.getMusherByTeamId(team.id);
        const image = team.images && team.images.length > 0 ? team.images[0] : {};
        const raceClass = raceStore.getClassByTeamId(team.id);

        const completed = currentResults.distanceKm / currentClass.distance * 100;
        const hasStarted = completed > 0 ? 1 : 0;
        const isDone = completed >= 100 ? 1 : 0;

        let currentSpeed;
        let updated;
        let isActive;
        if (line && line.currentLocation && line.currentLocation.coords) {
            currentSpeed = line.currentLocation.coords.speed * 3.6;
            updated = line.currentLocation.timestamp;
            isActive = true;
        }
        let toFinish = raceClass.distance - currentResults.distanceKm;
        if (toFinish < 0) {
            toFinish = 0;
        }

        // const currentSpeed = util.format(line.currentLocation.coords.speed * 3.6, 1);
        // avgDogs: 9.777777777777779
        // distanceKm: 268.3432364824406
        // duration: 67524.34002106966
        // elevation: 5287.123805500813
        // flatTotalKcal: 4971.658025959346
        // images: Array(32) [ Proxy, Proxy, Proxy, … ]
        // loadIndex: 1446
        // rest: 2306
        // speedAvg: 14.820718823260847
        // totalKcalRelated: 1225.314525471911
        // weightedTotalKcal: 11344.398317216459

        return (<>
            <div class='row border-top mt-2' style='font-size: 0.7em;'>
                <div class='col-2 p-0 text-center'>
                    <h2>{idx + 1}</h2>
                </div>
                <div class='col-2 p-0 text-center'>
                    <div
                        class={`text-center text-muted pt-2 h-100`}
                        style={`background-image: url("${image.s3SmallLink}"); background-size: cover;`}
                        onClick={this.toggleInfoOnMusher} data-musherid={team.id}
                    >
                        {!image.s3SmallLink && <>
                            <span class='font-weight-lighter' style='font-size: 35px;'>
                                {util.ucfirst(String(musher.firstname), true)}
                                {util.ucfirst(String(musher.lastname), true)}
                            </span>
                        </>}
                    </div>
                </div>
                <div class='col-8'>
                    <div class='row'>
                        <div class='col-6 px-1'>
                            <span class='font-weight-bold'>{musher.firstname} {musher.lastname}</span><br />
                            <span class='font-weight-lighter'>{team.name}</span><br />
                            {raceClass && raceClass.title && <>
                                <span class='font-weight-lighter'>{raceClass.title}</span>
                            </>}
                        </div>
                        <div class='col-3 p-0 text-center'>
                            <span class='font-weight-lighter'>km</span><br />
                            <span style='font-size: 2.0em;'>{util.format(currentResults.distanceKm, 0)}</span>
                        </div>
                        <div class='col-3 p-0 text-center'>
                            <span class='font-weight-lighter'>to finish</span><br />
                            <span style='font-size: 2.0em;'>
                                {toFinish === 0 ? <i class='fas fa-flag-checkered text-success' /> : <>{util.format(toFinish, 0)}</>}
                            </span>
                        </div>
                    </div>
                    <div class='row border-top'>
                        <div class='col-3 p-0 text-center'>
                            <span class='font-weight-lighter'>Rest</span><br />
                            {util.secToHms(currentResults.rest)}
                        </div>
                        <div class='col-3 p-0 text-center'>
                            <span class='font-weight-lighter'>Speed</span><br />
                            {currentSpeed ? <>{util.format(currentSpeed, 1)} km/t</> : '-'}
                        </div>
                        <div class='col-3 p-0 text-center'>
                            <span class='font-weight-lighter'>Status</span><br />
                            {line.isInProgress === 1 ? <span class='text-primary'>running</span> : ''}
                            {line.isInProgress === 2 ? <span class='text-danger'>resting</span> : ''}
                            {toFinish === 0 ? <>
                                {!line.isInProgress ? <span class='text-success'>FINISH</span> : ''}
                            </> : <>
                                {!line.isInProgress ? <>
                                    {hasStarted ? <span class='text-danger'>resting</span> : <span class='text-muted'>NYS</span>}
                                </> : ''}
                            </>}
                            
                        </div>
                        <div class='col-3 p-0 text-center align-bottom'>
                            <h5>
                                {team.dogs && team.dogs ? util.format(team.dogs.length, 0) : '-'} <i class='fas fa-dog text-muted ml-2' />
                            </h5>
                        </div>
                    </div>
                </div>
            </div>
            <div class='row bg-secondary text-white' style='font-size: 0.8em;'>
                <div class='col-6 text-left' style='font-size: 0.8em;'>
                    {isActive && <>
                        Updated: {util.formatDistance(updated, new Date())} ago
                    </>}
                </div>
                <div class='col-6 text-right'>
                    <span class='float-right' onClick={this.toggleFocusOnMusher} data-musherid={team.id}>{focusOnMushers.indexOf(team.id) > -1 ? <i class='fas fa-star text-warning' /> : <i class='far fa-star' />}</span>
                    <span class='float-right mr-2' onClick={this.toggleInfoOnMusher} data-musherid={team.id}>{infoOnMushers.indexOf(team.id) > -1 ? <i class='fas fa-info-circle text-warning' /> : <i class='fas fa-info-circle' />}</span>

                    {isActive && line.currentLocation.battery.level > 0 && <>
                        <span class='float-right mr-2'>
                            <i class='fas fa-battery-three-quarters text-muted ml-2 mr-1' />
                            {util.format(line.currentLocation.battery.level * 100, 0)}%
                        </span>
                    </>}

                </div>
            </div>
        </>);
    }
}

export default ListMusher;
